import Util from '@baifendian/adhere-util';
import LineStringGeometry from '../geom/LineStringGeometry';
import MulitLineStringGeometry from '../geom/MulitLineStringGeometry';
import MulitPointGeometry from '../geom/MulitPointGeometry';
import MulitPolygonGeometry from '../geom/MulitPolygonGeometry';
import PointGeometry from '../geom/PointGeometry';
import PolygonGeometry from '../geom/PolygonGeometry';
import { GeoJSONType, } from '../types';
export default {
    /**
     * parse - GeoJSON转IFeature[]
     * @param geoJSON
     * @param onForeachGeom
     */
    parse: function (geoJSON, onForeachGeom) {
        /**
         * Recursion
         * @param node
         * @param handler
         * @param features
         * @constructor
         */
        function Recursion(node, handler, features) {
            var feature = null;
            /**
             * Point
             * MultiPoint
             * LineString
             * MultiLineString
             * Polygon
             * MultiPolygon
             * GeometryCollection
             * Feature
             * FeatureCollection
             */
            if (node.type === GeoJSONType.Point) {
                var geom = new PointGeometry({
                    lng: node.coordinates[0],
                    lat: node.coordinates[1],
                });
                feature = handler(geom);
                feature.setId(Util.uuid());
                features.push(feature);
            }
            else if (node.type === GeoJSONType.MultiPoint) {
                var geom = new MulitPointGeometry(node.coordinates.map(function (coordinate) { return ({
                    lng: coordinate[0],
                    lat: coordinate[1],
                }); }));
                feature = handler(geom);
                feature.setId(Util.uuid());
                features.push(feature);
            }
            else if (node.type === GeoJSONType.LineString) {
                var geom = new LineStringGeometry({
                    point1: {
                        lng: node.coordinates[0][0],
                        lat: node.coordinates[0][1],
                    },
                    point2: {
                        lng: node.coordinates[1][0],
                        lat: node.coordinates[1][1],
                    },
                });
                feature = handler(geom);
                feature.setId(Util.uuid());
                features.push(feature);
            }
            else if (node.type === GeoJSONType.MultiLineString) {
                var geom = new MulitLineStringGeometry(node.coordinates.map(function (coordinate) { return ({
                    point1: {
                        lng: coordinate[0][0],
                        lat: coordinate[0][1],
                    },
                    point2: {
                        lng: coordinate[1][0],
                        lat: coordinate[1][1],
                    },
                }); }));
                feature = handler(geom);
                feature.setId(Util.uuid());
                features.push(feature);
            }
            else if (node.type === GeoJSONType.Polygon) {
                var geom = new PolygonGeometry(node.coordinates.map(function (coordinate) { return ({
                    lng: coordinate[0],
                    lat: coordinate[1],
                }); }));
                feature = handler(geom);
                feature.setId(Util.uuid());
                features.push(feature);
            }
            else if (node.type === GeoJSONType.MultiPolygon) {
                var geom = new MulitPolygonGeometry(node.coordinates.map(function (coordinate) {
                    return coordinate.map(function (p) { return ({
                        lng: p[0],
                        lat: p[1],
                    }); });
                }));
                feature = handler(geom);
                feature.setId(Util.uuid());
                features.push(feature);
            }
            else if (node.type === GeoJSONType.GeometryCollection) {
                node.geometries.forEach(function (geom) {
                    Recursion(geom, onForeachGeom, features);
                });
            }
            else if (node.type === GeoJSONType.Feature) {
                Recursion(node.geometry, function (g) {
                    var f = onForeachGeom(g);
                    f.setId(node.id);
                    f.setProperties(node.properties);
                    return f;
                }, features);
            }
            else if (node.type === GeoJSONType.FeatureCollection) {
                node.features.forEach(function (feature) {
                    Recursion(feature, onForeachGeom, features);
                });
            }
        }
        var features = [];
        Recursion(geoJSON, onForeachGeom, features);
        return features;
    },
    /**
     * stringify - features转GeoJSON
     * @param features
     * @return GeoJSONNode
     */
    stringify: function (features) {
        function getCoordinatesByType(geometry) {
            var mapping = new Map([
                [GeoJSONType.Point, getPointCoordinates],
                [GeoJSONType.MultiPoint, getMulitPointCoordinates],
                [GeoJSONType.LineString, getLineStringCoordinates],
                [GeoJSONType.MultiLineString, getMulitLineStringCoordinates],
                [GeoJSONType.Polygon, getPolygonCoordinates],
                [GeoJSONType.MultiPolygon, getMulitPolygonCoordinates],
            ]);
            var type = geometry.getType();
            // @ts-ignore
            return mapping.get(type)(geometry);
        }
        function getPointCoordinates(geometry) {
            var coordinates = geometry.getCoordinates();
            return [coordinates.lng, coordinates.lat];
        }
        function getMulitPointCoordinates(geometry) {
            var coordinates = geometry.getCoordinates();
            return coordinates.map(function (coordinate) { return [coordinate.lng, coordinate.lat]; });
        }
        function getLineStringCoordinates(geometry) {
            var coordinates = geometry.getCoordinates();
            return [
                [coordinates.point1.lng, coordinates.point1.lat],
                [coordinates.point2.lng, coordinates.point2.lat],
            ];
        }
        function getMulitLineStringCoordinates(geometry) {
            var coordinates = geometry.getCoordinates();
            return coordinates.map(function (coordinate) { return [
                [coordinate.point1.lng, coordinate.point1.lat],
                [coordinate.point2.lng, coordinate.point2.lat],
            ]; });
        }
        function getPolygonCoordinates(geometry) {
            var coordinates = geometry.getCoordinates();
            return coordinates.map(function (coordinate) { return [coordinate.lng, coordinate.lat]; });
        }
        function getMulitPolygonCoordinates(geometry) {
            var coordinates = geometry.getCoordinates();
            return coordinates.map(function (coordinate) { return coordinate.map(function (p) { return [p.lng, p.lat]; }); });
        }
        var geoJSON = {
            type: GeoJSONType.FeatureCollection,
            features: [],
        };
        var filterType = [];
        for (var key in GeoJSONType) {
            filterType.push(key);
        }
        features.forEach(function (feature) {
            var type = feature.getGeometry().getType();
            if (filterType.includes(type)) {
                geoJSON.features.push({
                    type: GeoJSONType.Feature,
                    geometry: {
                        // @ts-ignore
                        type: type,
                        coordinates: getCoordinatesByType(feature.getGeometry()),
                    },
                    id: feature.getId(),
                    properties: feature.getProperties(),
                });
            }
        });
        return geoJSON;
    },
};
